body {
  font-family: 'Segoe UI', 'Roboto', Arial, sans-serif;
  background: #f7fafc;
  margin: 0;
  padding: 0;
}

.container {
  width: 100vw;
  min-height: 100vh;
  background: linear-gradient(135deg, #e3eefd 0%, #f7fafc 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 0;
  box-sizing: border-box;
  gap: 0;
}

.word-section, .info-section, .spelling-section, .related-section, #currentWord, h2 {
  word-break: break-word;
  overflow-wrap: anywhere;
  white-space: normal;
  max-width: 100%;
  box-sizing: border-box;
}

#currentWord, h2 {
  max-width: 100%;
  overflow-wrap: anywhere;
  word-break: break-word;
  white-space: normal;
}

.related-section, #relatedWords {
  width: 100%;
  max-width: 100%;
  overflow-x: auto;
  display: block;
}

#relatedWords {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  overflow-x: auto;
  padding-bottom: 2px;
}

@media (max-width: 600px) {
  .container {
    padding: 0 0.2rem;
    min-width: 0;
  }
  .word-section, .info-section {
    padding: 8px 2px !important;
    max-width: 100vw !important;
    min-width: 0 !important;
  }
  .spelling-section {
    flex-direction: column !important;
    align-items: stretch !important;
    gap: 8px !important;
    margin-bottom: 12px !important;
    width: 100%;
    max-width: 100vw;
  }
  .speech-buttons-col, .input-col {
    min-width: 0 !important;
    max-width: 100vw;
    width: 100%;
    gap: 10px !important;
  }

  h1 {
    font-size: 1.1rem !important;
    margin-bottom: 0.8rem !important;
  }
  #currentWord {
    font-size: 1.1rem !important;
    letter-spacing: 1px !important;
  }
  .next-button, .say-button, .word-button, button {
    font-size: 0.95rem !important;
    padding: 8px 8px !important;
    min-width: 0 !important;
    width: 100%;
    box-sizing: border-box;
  }
  input[type="text"] {
    font-size: 0.95rem !important;
    padding: 8px 6px !important;
    min-width: 0 !important;
    width: 100%;
    box-sizing: border-box;
  }
  .related-section {
    flex-direction: column !important;
    gap: 2px !important;
    max-width: 100vw !important;
    overflow-x: auto !important;
  }
  #relatedWords {
    flex-wrap: nowrap !important;
    overflow-x: auto !important;
    width: 100%;
    max-width: 100vw;
    gap: 4px !important;
  }
}

@media (max-width: 600px) {
  .container {
    padding: 0 0.5rem;
    min-width: 0;
  }
  .word-section, .info-section {
    padding: 14px 8px !important;
    max-width: 98vw !important;
    min-width: 0 !important;
  }
  .spelling-section {
    flex-direction: column !important;
    align-items: stretch !important;
    gap: 12px !important;
    margin-bottom: 20px !important;
  }
  h1 {
    font-size: 1.5rem !important;
    margin-bottom: 1.2rem !important;
  }
  #currentWord {
    font-size: 1.3rem !important;
    letter-spacing: 1.5px !important;
  }
  .next-button, .say-button, .word-button, button {
    font-size: 1rem !important;
    padding: 10px 12px !important;
    min-width: 0 !important;
    width: 100%;
    box-sizing: border-box;
  }
  input[type="text"] {
    font-size: 1rem !important;
    padding: 10px 8px !important;
    min-width: 0 !important;
    width: 100%;
    box-sizing: border-box;
  }
  .related-section {
    flex-direction: column !important;
    gap: 4px !important;
  }
}

.word-section, .info-section, .spelling-section {
  word-break: break-word;
  overflow-wrap: anywhere;
}

h1 {
  font-size: 2.7rem;
  text-align: center;
  color: #2b4365;
  letter-spacing: 2px;
  margin: 40px 0 18px 0;
  font-weight: 700;
}

.word-section {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f1f6fb;
  padding: 24px 32px;
  border-radius: 18px;
  margin-bottom: 0;
  width: 95vw;
  max-width: 650px;
  margin: 0 auto 24px auto;
  box-shadow: 0 2px 10px rgba(49,130,206,0.04);
  gap: 28px;
}

#currentWord {
  font-size: 2.8rem;
  color: #3b5998;
  font-weight: 700;
  letter-spacing: 3px;
  margin: 0;
  flex: 1;
  text-align: left;
}

.next-button {
  padding: 16px 32px;
  font-size: 1.3rem;
  border: none;
  border-radius: 10px;
  background: linear-gradient(90deg, #3182ce 0%, #63b3ed 100%);
  color: #fff;
  font-weight: 700;
  cursor: pointer;
  box-shadow: 0 4px 16px rgba(49,130,206,0.10);
  transition: background 0.18s, transform 0.12s;
  margin-left: 20px;
}
.next-button:hover {
  background: linear-gradient(90deg, #2563eb 0%, #4299e1 100%);
  transform: translateY(-2px) scale(1.06);
}

.info-section {
  margin-top: 0;
  background: #f8fafc;
  border-radius: 14px;
  padding: 28px 24px 20px 24px;
  box-shadow: 0 1px 4px rgba(44, 62, 80, 0.04);
  width: 95vw;
  max-width: 650px;
  margin: 0 auto 0 auto;
}

.info-section p {
  margin: 16px 0;
  font-size: 1.2rem;
  color: #2d3748;
  line-height: 1.6;
}

.related-section {
  margin-top: 28px;
}

.related-section strong {
  color: #2563eb;
  font-size: 1.04rem;
}

#relatedWords {
  margin-top: 14px;
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.word-button {
  margin: 0;
  padding: 12px 22px;
  border-radius: 10px;
  border: 1.5px solid #cbd5e0;
  background: #e3eefd;
  color: #234e70;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.18s, color 0.15s, box-shadow 0.15s;
  box-shadow: 0 2px 5px rgba(49,130,206,0.06);
}
.word-button:hover, .word-button:active {
  background: #bee3f8;
  color: #1a365d;
  border-color: #3182ce;
}

@media (max-width: 900px) {
  .container {
    padding: 0;
    min-height: 100vh;
  }
  .word-section, .info-section {
    width: 99vw;
    max-width: 99vw;
    padding-left: 2vw;
    padding-right: 2vw;
  }
  h1 {
    font-size: 2.1rem;
    margin-top: 24px;
  }
}

@media (max-width: 600px) {
  .container {
    padding: 0;
    min-height: 100vh;
  }
  .word-section, .info-section {
    border-radius: 0;
    width: 100vw;
    max-width: 100vw;
    padding-left: 2vw;
    padding-right: 2vw;
  }
  .word-section {
    flex-direction: column;
    gap: 12px;
    padding-top: 18px;
    padding-bottom: 10px;
  }
  h1 {
    font-size: 1.4rem;
    margin-top: 18px;
  }
  .next-button {
    width: 100%;
    margin-left: 0;
    margin-top: 12px;
    font-size: 1.1rem;
    padding: 13px 0;
  }
  #currentWord {
    font-size: 2rem;
    text-align: center;
  }
  .info-section p {
    font-size: 1rem;
  }
  .word-button {
    padding: 10px 10px;
    font-size: 1rem;
  }
}

